<template>
  <div class="form-wrap">
    <div class="head" style="margin-top:0">
      备课基本信息
      <span class="back-btn" @click="back">返回</span>
    </div>
    <div class="form-container">
      <el-form :model="form" :rules="rules" ref="form" label-width="100px">
        <el-row :gutter="5">
          <el-col :span="8">
            <el-form-item label="章节名称：" prop="PrepareName">
              <el-input class="prepare-form-input" v-model="form.PrepareName" clearable></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="单元：" prop="Content">
              <el-select class="prepare-form-input" v-model="form.Content" placeholder="请选择教学内容">
                <el-option
                  v-for="(item,index) in TeachingContents"
                  :key="index"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="课程：">
              <!-- {{CourseName}} -->
              <el-input class="prepare-form-input" v-model="CourseName" readonly></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="5">
          <el-col :span="8">
            <el-form-item label="教室：">
              <!-- {{ClsRoomName}} -->
              <el-input class="prepare-form-input" v-model="ClsRoomName" readonly></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="日期：">
              <!-- {{CSDate}} -->
              <el-input class="prepare-form-input" v-model="CSDate" readonly></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="课节：">
              <!-- 第{{CSLesson}}节 -->
              <el-input class="prepare-form-input" v-model="CSLesson" readonly></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-form-item label="图片：" class="attachment-line">
            <div class="dialog-img">
              <img :src="prepareSrc" :draggable="false" />
            </div>
            <div class="attachment">
              <el-upload
                class="upload-img"
                :file-list="fileList"
                list-type="picture"
                :limit="1"
                action
                :on-change="handlePreview"
                :auto-upload="false"
                :on-remove="closeHandler"
              >
                <el-button class="create-btn" type="primary" size="small">选择图片</el-button>
                <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
              </el-upload>
            </div>
          </el-form-item>
        </el-row>
        <el-row>
          <el-button class="create-btn" type="primary" size="small" @click="comfirmFormHandler">保 存</el-button>
        </el-row>
      </el-form>
    </div>
    <div class="head">
      备课资源
      <el-button
        class="create-btn"
        type="primary"
        size="mini"
        style="margin-left:50px;"
        @click="AddRes"
      >添 加</el-button>
    </div>
    <div class="form-container">
      <!-- <prepare-resources></prepare-resources> -->
      <el-table ref="resDatas" :data="resDatas" style="width: 100%">
        <el-table-column label="类型" prop="PSType">
          <template slot-scope="scope">
            <span v-if="scope.row.PSType == 1">教学课件</span>
            <span v-if="scope.row.PSType == 2">教案</span>
            <span v-if="scope.row.PSType == 3">课件</span>
            <span v-if="scope.row.PSType == 4">微课</span>
            <span v-if="scope.row.PSType == 5">习题</span>
            <span v-if="scope.row.PSType == 6">图文资源</span>
          </template>
        </el-table-column>
        <el-table-column label="标题" prop="PSTitle"></el-table-column>
        <el-table-column label="操作" prop="oprate" width="100px">
          <template slot-scope="scope">
            <div class="btns">
              <i class="iconfont icon-pencil" @click="handleEditRes(scope.$index, scope.row)"></i>
              <i
                class="iconfont icon-shanchu"
                @click="handleDelete(scope.$index, scope.row, 'Res')"
              ></i>
            </div>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="head">
      备课活动
      <el-button
        class="create-btn"
        type="primary"
        size="mini"
        style="margin-left:50px;"
        @click="AddCa"
      >添 加</el-button>
    </div>
    <div class="form-container">
      <el-table ref="caDatas" :data="caDatas" style="width: 100%">
        <el-table-column label="类型" prop="CAType">
          <template slot-scope="scope">
            <span v-if="scope.row.CAType == 1">投票</span>
            <span v-if="scope.row.CAType == 2">头脑风暴</span>
            <span v-if="scope.row.CAType == 3">答疑讨论</span>
            <span v-if="scope.row.CAType == 4">测试</span>
            <span v-if="scope.row.CAType == 5">作业</span>
            <span v-if="scope.row.CAType == 6">举手</span>
            <span v-if="scope.row.CAType == 7">抢答</span>
            <span v-if="scope.row.CAType == 8">选人</span>
          </template>
        </el-table-column>
        <el-table-column label="标题" prop="CATitle"></el-table-column>
        <el-table-column label="操作" prop="oprate" width="100px">
          <template slot-scope="scope">
            <div class="btns">
              <i class="iconfont icon-pencil" @click="handleEditCa(scope.$index, scope.row)"></i>
              <i class="iconfont icon-shanchu" @click="handleDelete(scope.$index, scope.row, 'Ca')"></i>
            </div>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-dialog
      title="添加/修改备课资源"
      width="520px"
      :close-on-click-modal="false"
      :visible.sync="isResFormShow"
      :before-close="ResetResForm"
    >
      <el-form :model="ResForm" :rules="ResRules" ref="ResForm" :label-width="formLabelWidth">
        <el-form-item label="类型" prop="pstype">
          <el-select :default-first-option="true" v-model="ResForm.pstype" placeholder="请选择类型">
            <el-option
              v-for="item in ResTypeOpts"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="素材" :label-width="formLabelWidth" prop="pnode">
          <Treeselect
            v-model="ResForm.pnode"
            :multiple="false"
            :options="pOpts"
            :load-options="loadOptions"
            valueFormat="object"
            :clearable="true"
            placeholder="请选择素材"
          ></Treeselect>
          <el-upload
            class="upload-demo"
            ref="upload"
            action
            :auto-upload="false"
            :before-upload="beforeUpload"
          >
            <el-button type="primary" icon="el-icon-plus" slot="trigger" size="small">选取文件</el-button>
            <el-button type="success" icon="el-icon-upload" size="small" @click="submitUpload">上传文件</el-button>
            <div class="el-upload__tip" slot="tip">文件最大10M，超过限制的文件请到网盘上传</div>
          </el-upload>
        </el-form-item>
        <el-form-item label="标题" prop="pstitle">
          <el-tooltip class="item" effect="dark" placement="right">
            <el-input v-model="ResForm.pstitle" clearable></el-input>
          </el-tooltip>
        </el-form-item>
        <el-form-item label="内容" prop="pscontent">
          <el-input type="textarea" rows="5" v-model="ResForm.pscontent" clearable></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button type="primary" size="small" @click="ComfirmResFormHandler()">
          <i class="el-icon-check el-icon--left"></i>确 定
        </el-button>
        <el-button @click="ResetResForm" size="small">
          <i class="el-icon-close el-icon--left"></i>取 消
        </el-button>
      </div>
    </el-dialog>
    <el-dialog
      title="添加/修改备课活动"
      width="520px"
      :close-on-click-modal="false"
      :visible.sync="isCaFormShow"
      :before-close="ResetCaForm"
    >
      <el-form :model="CaForm" :rules="CaRules" ref="CaForm" :label-width="formLabelWidth">
        <el-form-item label="类型" prop="catype">
          <el-select :default-first-option="true" v-model="CaForm.catype" placeholder="请选择类型">
            <el-option
              v-for="item in CaTypeOpts"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="试卷" prop="examsid">
          <el-select :default-first-option="true" v-model="CaForm.examsid" placeholder="请选择">
            <el-option label="无" value></el-option>
            <el-option
              v-for="item in ExamsOpts"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="标题" prop="catitle">
          <el-tooltip class="item" effect="dark" placement="right">
            <el-input v-model="CaForm.catitle" clearable></el-input>
          </el-tooltip>
        </el-form-item>
        <el-form-item label="内容" prop="cadesc">
          <el-input type="textarea" rows="5" v-model="CaForm.cadesc" clearable></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button type="primary" size="small" @click="ComfirmCaFormHandler()">
          <i class="el-icon-check el-icon--left"></i>确 定
        </el-button>
        <el-button @click="ResetCaForm" size="small">
          <i class="el-icon-close el-icon--left"></i>取 消
        </el-button>
      </div>
    </el-dialog>
    <!-- 确认删除提示 -->
    <el-dialog
      class="delete-dialog"
      title="智慧校园提示您"
      :close-on-click-modal="false"
      :visible.sync="confirmDeleteDialog"
      center
      width="400px"
    >
      <span>确定要彻底删除吗？</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="confirmDeleteDialog = false">取 消</el-button>
        <el-button type="primary" @click="confirmDeleteHandler">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import Treeselect from "@riophae/vue-treeselect";
import { LOAD_CHILDREN_OPTIONS } from "@riophae/vue-treeselect";
import "@riophae/vue-treeselect/dist/vue-treeselect.css";
import http from "../../../../utils/http";
import api from "../../../../utils/api";
import { formatDate } from "../../../../utils/utils.js";
export default {
  components: {
    Treeselect
  },
  data() {
    //验证素材节点是目录还是文件
    let ValidateNodeIsMaterial = async (rule, node, callback) => {
      if (node != undefined) {
        if (node.id != "" && node.id != undefined) {
          if (!node.isMaterial)
            callback(new Error("当前选中的是目录，请选择文件"));
        } else {
          callback();
        }
      }
    };
    return {
      prepareID: "",
      csid: "",
      supportingID: "",
      caID: "",
      CourseName: "",
      ClsRoomName: "",
      CSDate: "",
      CSLesson: "",
      fileName: "",
      prepareSrc: "",
      isDelRes: true,
      isResFormShow: false,
      isCaFormShow: false,
      confirmDeleteDialog: false,
      formLabelWidth: "80px",
      rootDirID: "",
      file: "",
      fileList: [],
      TeachingContents: [],
      resDatas: [],
      caDatas: [],
      ExamsOpts: [],
      pOpts: [
        {
          id: "",
          label: "",
          isMaterial: false,
          children: null
        }
      ],
      form: {
        PrepareName: "",
        Content: ""
      },
      rules: {
        PrepareName: [
          {
            required: true,
            min: 2,
            max: 30,
            message: "长度在 2 到 30 个字符",
            trigger: "blur"
          }
        ],
        Content: [
          {
            required: true,
            message: "请选择教学内容",
            trigger: "blur"
          }
        ]
      },
      ResForm: {
        pstype: "",
        pnode: { id: "", label: "" },
        pstitle: "",
        pscontent: ""
      },
      ResRules: {
        pstype: [
          {
            required: true,
            message: "请选择类型",
            trigger: "blur"
          }
        ],
        pnode: [
          {
            validator: ValidateNodeIsMaterial,
            trigger: "blur"
          }
        ]
      },
      CaForm: {
        catype: "",
        examsid: "",
        catitle: "",
        cacontent: ""
      },
      CaRules: {
        catype: [
          {
            required: true,
            message: "请选择类型",
            trigger: "blur"
          }
        ]
      },
      ResTypeOpts: [
        {
          value: 1,
          label: "教学课件"
        },
        {
          value: 2,
          label: "教案"
        },
        {
          value: 3,
          label: "课件"
        },
        {
          value: 4,
          label: "微课"
        },
        {
          value: 5,
          label: "习题"
        },
        {
          value: 6,
          label: "图文资源"
        }
      ],
      CaTypeOpts: [
        {
          value: 1,
          label: "投票"
        },
        {
          value: 2,
          label: "头脑风暴"
        },
        {
          value: 3,
          label: "答疑讨论"
        },
        {
          value: 4,
          label: "测试"
        },
        {
          value: 5,
          label: "作业"
        },
        {
          value: 6,
          label: "举手"
        },
        {
          value: 7,
          label: "抢答"
        },
        {
          value: 8,
          label: "选人"
        }
      ]
    };
  },
  mounted() {
    this.prepareID = this.$route.query.pid;

    this.getDatasByUID();

    if (this.prepareID != "" && this.prepareID != undefined) {
      this.getPrepareData();
    } else {
      this.csid = this.$route.query.csid;
      this.getClsSKDData();
    }
  },
  methods: {
    back() {
      this.$router.back();
    },
    //获取备课信息
    getPrepareData: async function() {
      const res = await http.get(api.getPrepareData, {
        id: this.prepareID,
        Token: http.getToken()
      });
      if (res.Result) {
        if (res.Data == null) return;

        let downloadURL = await api.GetDownloadFileURL();
        this.csid = res.Data.CSID;
        this.form.PrepareName = res.Data.PrepareName;
        this.form.Content = res.Data.TCID;
        this.fileName = res.Data.PPic;
        this.prepareSrc = `${downloadURL}?filename=${this.fileName}&fileType=10`;
        this.getClsSKDData();
        this.getPrepareResDatas();
        this.getClsActivesDatas();
      }
    },

    //获取课程
    getClsSKDData: async function() {
      const res = await http.get(api.GetClsSKDData, {
        id: this.csid,
        Token: http.getToken()
      });
      if (res.Result) {
        if (res.Data == null) return;

        this.CourseName = res.Data.Key.CSTitle;
        this.ClsRoomName = res.Data.ClsRoomName;
        this.CSDate = formatDate(res.Data.Key.CSDate, "yyyy-MM-dd");
        this.CSLesson = "第" + res.Data.Key.CSLesson + "节";
      }
    },

    //获取教学内容
    getDatasByUID: async function() {
      const res = await http.get(api.getDatasByUID, { Token: http.getToken() });
      if (res.Result) {
        if (res.Datas == null) return;

        this.TeachingContents = [];
        res.Datas.forEach(element => {
          this.TeachingContents.push({
            value: element.TCID,
            label: element.TCTitle,
            Data: element
          });
        });
      }
    },

    //获取备课资源数据
    getPrepareResDatas: async function() {
      const res = await http.get(api.GetPrepareResDatas, {
        prepareID: this.prepareID,
        Token: http.getToken()
      });
      if (res.Result) {
        if (res.Datas == null) return;

        this.resDatas = res.Datas;
      }
    },

    //获取备课活动数据
    getClsActivesDatas: async function() {
      const res = await http.get(api.GetClsActivesDatas, {
        prepareID: this.prepareID,
        Token: http.getToken()
      });
      if (res.Result) {
        if (res.Datas == null) return;

        this.caDatas = res.Datas;
      }
    },

    //表单确认
    comfirmFormHandler() {
      this.$refs["form"].validate(valid => {
        if (valid) {
          let params = {
            id: this.prepareID,
            csid: this.$route.query.csid,
            tcid: this.form.Content,
            name: this.form.PrepareName,
            ppic: this.fileName
          };

          this.saveData(params);
        } else {
          this.$message({
            showClose: true,
            message: "请输入正确格式",
            type: "error"
          });
        }
      });
    },

    //保存备课基本信息
    saveData: async function(params) {
      const res = await http.post(api.savePrepareData, params);
      if (!res.Result) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        });
      } else {
        this.$message({
          showClose: true,
          message: "保存数据成功",
          type: "success"
        });
        this.prepareID = res.Data;
      }
    },

    async uploadHandler() {
      let fd = new FormData();
      fd.append("stream", this.file.raw);
      fd.append("type", 10);
      let res = await http.postFormData(api.UploadFile, fd);
      if (res.type) {
        this.$message({
          message: res.message,
          type: res.type
        });
      } else {
        let downloadURL = await api.GetDownloadFileURL();
        this.fileName = res.Data.split(".")[0];
        this.prepareSrc =
          downloadURL + "?filename=" + this.fileName + "&fileType=10";
      }
    },

    closeHandler() {
      this.file = "";
      this.fileName = "";
      this.fileList = [];
      this.prepareSrc = "";
    },
    handlePreview(file, fileList) {
      this.file = file;
      this.uploadHandler();
    },

    //添加备课资源
    AddRes() {
      this.isResFormShow = true;
      this.supportingID = "";
      this.getMaterialRootDir();
    },

    //获取素材根目录
    getMaterialRootDir: async function() {
      const res = await http.get(api.GetMaterialRootDir, {
        token: http.getToken()
      });
      if (res.Result) {
        this.pOpts = [
          {
            id: res.ID,
            label: res.Name,
            isMaterial: false,
            children: null
          }
        ];
        this.rootDirID = res.ID;
      } else {
        this.pOpts = [
          {
            id: "",
            label: "您还未开通网盘",
            isMaterial: false,
            children: null
          }
        ];
      }
    },

    //重置备课资源表单
    ResetResForm() {
      this.ResForm = {
        pstype: "",
        pstitle: "",
        pscontent: ""
      };

      this.$refs["ResForm"].resetFields();
      this.isResFormShow = false;
    },

    //备课资源表单确认
    ComfirmResFormHandler() {
      this.$refs["ResForm"].validate(valid => {
        if (valid) {
          let params = {
            id: this.supportingID,
            prepareid: this.prepareID,
            materialid:
              this.ResForm.pnode == undefined ? "" : this.ResForm.pnode.id,
            pstype: this.ResForm.pstype,
            pstitle: this.ResForm.pstitle,
            pscontent: this.ResForm.pscontent
          };
          this.saveRecData(params);
        } else {
          this.$message({
            showClose: true,
            message: "请输入正确格式",
            type: "error"
          });
        }
      });
    },

    //保存备课资源信息
    saveRecData: async function(params) {
      const res = await http.post(api.SavePrepareResData, params);
      if (!res.Result) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        });
      } else {
        this.$message({
          showClose: true,
          message: "保存数据成功",
          type: "success"
        });

        this.ResetResForm();
        this.getPrepareResDatas();
      }
    },

    //修改备课资源数据
    handleEditRes: async function(index, row) {
      this.isResFormShow = true;
      this.getMaterialRootDir();

      const res = await http.get(api.GetPrepareResData, {
        id: row.SupportingID,
        Token: http.getToken()
      });
      if (!res.Result) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        });
      } else {
        this.ResForm.pstype = res.Data.PrepareRes.PSType;
        if (res.Data.Material.MaterialID != "") {
          this.ResForm.pnode = {
            id: res.Data.Material.MaterialID,
            label:
              res.Data.Material.MaterialName +
              "." +
              res.Data.Material.MaterialFileExt,
            isMaterial: true
          };
        } else {
          this.ResForm.pnode = null;
        }
        this.ResForm.pstitle = res.Data.PrepareRes.PSTitle;
        this.ResForm.pscontent = res.Data.PrepareRes.PSContent;
        this.supportingID = res.Data.PrepareRes.SupportingID;
      }
    },

    //删除备课资源数据
    handleDelete: async function(index, row, type) {
      this.confirmDeleteDialog = true;
      if (type == "Res") {
        this.isDelRes = true;
        this.supportingID = row.SupportingID;
      } else {
        this.isDelRes = false;
        this.caID = row.CAID;
      }
    },

    //提交删除
    confirmDeleteHandler: async function() {
      if (this.isDelRes) {
        //删除备课资源
        const res = await http.post(api.DeletePrepareRes, {
          ids: this.supportingID,
          Token: http.getToken()
        });
        if (!res.Result) {
          this.$message({
            showClose: true,
            message: res.message,
            type: res.type
          });
        } else {
          this.confirmDeleteDialog = false;
          this.getPrepareResDatas();
        }
      } else {
        //删除备课活动
        const res = await http.post(api.DeleteClsActives, {
          ids: this.caID,
          Token: http.getToken()
        });
        if (!res.Result) {
          this.$message({
            showClose: true,
            message: res.message,
            type: res.type
          });
        } else {
          this.confirmDeleteDialog = false;
          this.getClsActivesDatas();
        }
      }
    },

    //加载素材目录
    async loadOptions({ action, parentNode, callback }) {
      if (action === LOAD_CHILDREN_OPTIONS) {
        const res = await http.get(api.GetDirFiles, {
          id: parentNode.id,
          Token: http.getToken()
        });
        if (res.type) {
          this.$message({
            showClose: true,
            message: res.message,
            type: res.type
          });
        } else {
          parentNode.children = [];
          if (res.Dirs != null && res.Files == null) {
            res.Dirs.forEach(element => {
              parentNode.children.push({
                id: element.DirectoryID,
                label: element.DirectoryName,
                isMaterial: false,
                children: null
              });
            });
          } else if (res.Files != null) {
            res.Files.forEach(element => {
              parentNode.children.push({
                id: element.MaterialID,
                label: element.MaterialName + "." + element.MaterialFileExt,
                isMaterial: true
              });
            });
          }
        }
      }
    },

    submitUpload() {
      this.$refs.upload.submit();
    },

    beforeUpload: async function(file) {
      //这里是重点，将文件转化为formdata数据上传
      let fd = new FormData();
      fd.append("stream", file);
      fd.append("dirid", this.rootDirID);
      fd.append("Token", http.getToken());
      const res = await http.postFormData(api.UploadPrepareMaterial, fd);
      if (!res.Result) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        });
      } else {
        this.$message({
          showClose: true,
          message: "上传成功",
          type: "success"
        });

        this.ResForm.pnode = {
          id: res.ID,
          label: res.Name,
          isMaterial: true
        };
      }

      return false;
    },

    //添加备课活动
    AddCa() {
      this.isCaFormShow = true;
      this.caID = "";
      this.getExamsAllDatas();
    },

    //加载试卷
    async getExamsAllDatas() {
      this.ExamsOpts = [];
      const res = await http.get(api.GetExamsAllDatas, {
        token: http.getToken()
      });
      if (res.Result) {
        res.Datas.forEach(element => {
          this.ExamsOpts.push({
            value: element.ExamsID,
            label: element.ExamsTitle,
            Data: element
          });
        });
      }
    },

    //重置备课活动表单
    ResetCaForm() {
      this.CaForm = {
        cstype: "",
        examsid: "",
        cstitle: "",
        cscontent: ""
      };

      this.$refs["CaForm"].resetFields();
      this.isCaFormShow = false;
    },

    //添加备课活动表单确认
    ComfirmCaFormHandler() {
      this.$refs["CaForm"].validate(valid => {
        if (valid) {
          let params = {
            id: this.caID,
            prepareid: this.prepareID,
            examsid: this.CaForm.examsid,
            catype: this.CaForm.catype,
            catitle: this.CaForm.catitle,
            cadesc: this.CaForm.cadesc,
            cavalue: 0
          };
          this.saveCaData(params);
        } else {
          this.$message({
            showClose: true,
            message: "请输入正确格式",
            type: "error"
          });
        }
      });
    },

    //保存备课活动信息
    saveCaData: async function(params) {
      const res = await http.post(api.SaveClsActivesData, params);
      if (!res.Result) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        });
      } else {
        this.$message({
          showClose: true,
          message: "保存数据成功",
          type: "success"
        });

        this.ResetCaForm();
        this.getClsActivesDatas();
      }
    },

    //修改备课活动数据
    handleEditCa: async function(index, row) {
      this.isCaFormShow = true;
      this.getMaterialRootDir();
      const res = await http.get(api.GetClsActivesData, {
        id: row.CAID,
        Token: http.getToken()
      });
      if (!res.Result) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        });
      } else {
        this.CaForm.catype = res.Data.CAType;
        this.CaForm.examsid = res.Data.ExamsID;
        this.CaForm.catitle = res.Data.CATitle;
        this.CaForm.cadesc = res.Data.CADesc;
        this.caID = res.Data.CAID;
      }
    }
  }
};
</script>
<style lang="less">
.form-wrap {
  width: 100%;
  background: #fff;
  padding: 25px;
  padding-top: 12px;
  .head {
    width: 100%;
    height: 32px;
    margin-top: 15px;
    text-align: left;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .form-container {
    width: 100%;
    margin-top: 15px;
    background: #f8f8f8;
    padding: 25px;
    position: relative;
    clear: both;
    .prepare-form-input {
      input {
        height: 28px !important;
      }
    }
    .el-tabs--border-card > .el-tabs__header {
      background: #f1f8ff !important;
    }
    .prepare-date {
      width: 100% !important;
    }
    .total-num {
      position: absolute;
      right: 36px;
      z-index: 1;
      top: 36px;
      font-size: 0.8rem;
    }
    .btns {
      width: 80px;
      display: flex;
      justify-content: space-around;
      text-align: center;
      .iconfont {
        font-size: 1.2rem !important;
        border-radius: 5px;
        padding: 2px 5px;
        color: #fff;
        &:hover {
          cursor: pointer;
        }
      }
      .icon-pencil {
        background: #63a9ff;
      }
      .icon-shanchu {
        background: #ff6363;
      }
    }
    .attachment-line {
      .el-form-item__content {
        line-height: 22px !important;
      }
      .attachment {
        .attachment-item {
          width: 90px;
          height: 105px;
          float: left;
          margin-right: 15px;
          .img-box {
            width: 77px;
            height: 88px;
            margin: 0 auto;
            background: #fff;
            border: 1px solid #ddd;
            color: #ddd;
            text-align: center;
            position: relative;
            user-select: none;
            .mask,
            img {
              width: 100%;
              height: 100%;
              position: absolute;
              left: 0;
              top: 0;
            }
            .icon-cha {
              width: 22px;
              height: 22px;
              background: rgba(0, 0, 0, 0.5);
              color: #fff;
              border: 1px solid #fff;
              border-radius: 50%;
              position: absolute;
              right: -11px;
              top: -11px;
              font-size: 0.8rem !important;
              &:hover {
                cursor: pointer;
              }
            }
            .bar {
              width: 88%;
              position: absolute;
              top: 56%;
              left: 50%;
              transform: translateX(-50%);
            }
            .progress-value {
              position: absolute;
              top: 22%;
              color: #fff;
              left: 50%;
              font-size: 1.2rem;
              transform: translateX(-50%);
            }
            .mask {
              background: rgba(0, 0, 0, 0.5);
            }
            .icon-tianjiafujian {
              font-size: 2.8rem;
              position: absolute;
              top: 38%;
              left: 50%;
              transform: translate(-50%);
            }
          }
          .text {
            margin-top: 2px;
            font-size: 0.9rem;
          }
          .tip {
            font-size: 0.9rem;
            color: #7ab2fd;
          }
          .done {
            color: #0dbc79;
          }
        }
      }

      .el-upload {
        display: flex;
        width: 225px;
      }

      .el-upload-dragger {
        width: 225px !important;
      }

      .el-upload__tip {
        margin-top: 0px;
        text-align: left;
      }
    }
    .active-container {
      width: 100%;
      background: #fff;
      border: 1px solid #dcdfe6;
      box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12), 0 0 6px 0 rgba(0, 0, 0, 0.04);
      .head-btns {
        width: 100%;
        height: 40px;
        background: #f1f8ff;
        display: flex;
        justify-content: space-around;
        .active-btn {
          width: 16.6%;
          align-self: center;
          .icon-toupiao1 {
            color: #3a97ff;
          }
          .icon-tounaofengbao {
            color: #0dbc79;
          }
          .icon-taolun {
            color: #905fea;
          }
          .icon-ceshi {
            color: #fcb34b;
          }
          .icon-zuoye {
            color: #ed7474;
          }
          .icon-jushou {
            color: #26d3bd;
          }
          &:hover {
            cursor: pointer;
          }
        }
      }
      .choose-bar {
        width: 100%;
        height: 40px;
        line-height: 40px;
        text-align: left;
        padding-left: 25px;
        border-top: 1px solid #dcdfe6;
        border-bottom: 1px solid #dcdfe6;
      }
      .box {
        width: 100%;
        padding: 0 25px;
        .activity-item {
          width: 100%;
          height: 115px;
          border-bottom: 1px dashed #aaa;
          padding: 32px 0;
          .icon-box {
            width: 50px;
            height: 50px;
            line-height: 50px;
            float: left;
            color: #fff;
            border-radius: 8px;
            .iconfont {
              font-size: 2rem !important;
            }
          }
          .tp {
            background: #3a97ff;
          }
          .tnfb {
            background: #0dbc79;
          }
          .dytl {
            background: #905fea;
          }
          .cs {
            background: #fcb34b;
          }
          .zy {
            background: #ed7474;
          }
          .ktbx {
            background: #26d3bd;
          }
          .detail {
            width: 92%;
            height: 50px;
            float: right;
            .detail-head {
              width: 100%;
              height: 50%;
              .text {
                font-weight: bold;
                float: left;
              }
              .tag {
                padding: 2px 5px;
                margin-left: 8px;
                border: 1px solid #3a97ff;
                float: left;
                font-size: 0.6rem;
              }
              .gonna {
                color: #3a97ff;
              }
              .ing {
                border-color: #0dbc79;
                color: #0dbc79;
              }
              .done {
                border-color: #ed7474;
                color: #ed7474;
              }
              .throwing {
                float: right;
                padding: 2px 5px;
                border-radius: 2px;
                background: #3a97ff;
                color: #fff;
                font-size: 0.8rem;
                &:hover {
                  cursor: pointer;
                }
              }
            }
            .detail-bottom {
              width: 100%;
              height: 50%;
              color: #aaa;
              font-size: 0.8rem;
              clear: both;
              padding-top: 8px;
              .text {
                float: left;
              }
              .btns {
                float: right;
                span {
                  margin-left: 8px;
                  &:hover {
                    cursor: pointer;
                    color: #0dbc79;
                  }
                }
              }
            }
          }
        }
      }
    }
    .dialog-img {
      width: 320px;
      height: 180px;
      margin-bottom: 10px;
      background-repeat: no-repeat;
      background-size: contain;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
}

.el-dialog .el-form-item__content {
  text-align: left;
}

.el-dialog--center .el-dialog__footer {
  text-align: right;
}
</style>



